import { useTranslation } from "react-i18next";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../InstituteAnalytic.module.css";
import { useState } from "react";
import StaffTeachingPlanSubjectList from "./StaffTeachingPlanSubjectList";

const StaffTeachingPlanCard = ({ staff, instituteId }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");

  const onClose = () => {
    setStep("");
  };
  return (
    <>
      <div
        className={style.staclc_card}
        style={{
          justifyContent: "space-between",
        }}
      >
        <div className={style.staclc_card_container}>
          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />
          <div className={style.staclc_card_inner}>
            <h6>
              {`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}
            </h6>
            <p>
              {t("employee_code")}
              {": "}
              {staff?.staffROLLNO ?? ""}
            </p>
          </div>
        </div>
        <div className={style.stc_stats}>
          <span onClick={() => setStep(1)}>4</span> /{" "}
          <span onClick={() => setStep(2)}>10</span>
        </div>
      </div>
      {step === 1 ? (
        <StaffTeachingPlanSubjectList
          label="teaching_plan_updated_subject"
          onClose={onClose}
          instituteId={instituteId}
          sid={staff?._id}
          category="Yes"
        />
      ) : step === 2 ? (
        <StaffTeachingPlanSubjectList
          label="teaching_plan_not_updated_subject"
          onClose={onClose}
          instituteId={instituteId}
          sid={staff?._id}
          category="No"
        />
      ) : null}
    </>
  );
};

export default StaffTeachingPlanCard;
